                </el-table>
                <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pages.pageNo"
                        :page-sizes="[8, 10, 20, 50]"
                        :page-size="pages.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="pages.total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <el-dialog
            title=""
            :visible.sync="editTsTimeShow"
            width="40%"
            center>
            <div style="text-align: center;height: 100px">
                <el-date-picker
                    size="small"
                    style="width: 80%"
                    v-model="tsTime"
                    type="datetime"
                    value-format="yyyy-MM-dd hh:mm:ss"
                    placeholder=""
                ></el-date-picker>
            </div>
            <span slot="footer" class="dialog-footer">
        <el-button @click="editTsTimeShow = false" size="small"> </el-button>
        <el-button type="primary" @click="setTsTime" size="small"> </el-button>
      </span>
        </el-dialog>

<!--        -->
        <el-dialog
            class="portraitInfo-dialog"
            :visible.sync="portraitInfoVisible"
            width="95%"
            top="5vh"
            :before-close="portraitInfoHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '' }}</span>
      </span>
            <div class="dialog-content">
                <div class="dialog-content-title">
                    <el-input
                        class="inputC"
                        placeholder=""
                        v-model="portraitInfoQueryForm.yhbh"
                        size="small"
                        clearable
                    ></el-input>
                    <el-button size="small" type="primary" @click="GetTdmgkhhx"></el-button>
                </div>
                <div class="dialog-content-main">
                    <div class="recommend">
                        <div class="main-title"></div>
                        <div class="text-item">
                            <div class="text">{{',,:'}}</div>
                            <div class="value">{{portraitInfoQueryForm.dxtznr}}</div>
                        </div>
                        <div class="text-item">
                            <div class="text">{{':'}}</div>
                            <div class="value">{{portraitInfoQueryForm.dxfssj}}</div>
                        </div>
                        <div class="text-item">
                            <div class="text">{{':'}}</div>
                            <div class="value">{{portraitInfoQueryForm.lxfs}}</div>
                        </div>
                    </div>
                    <div class="portrait-label">
                        <div class="main-content">
                            <div class="main-title"></div>
                            <div style="width: 100%;height: 500px">
                                <word-cloud :wordCloudData="wordCloudData" v-if="wordCloudDataVisible"></word-cloud>
                            </div>
                        </div>
                    </div>
                    <div class="calendar">
                        <div class="main-content">
                            <div class="td_title"></div>
                            <el-table
                                :data="tableData1"
                                border
                                style="width: 100%">
                                <el-table-column prop="ydlb" label=""></el-table-column>
                                <el-table-column prop="dydj" label=""></el-table-column>
                                <el-table-column prop="ldcs" label="95598"></el-table-column>
                            </el-table>
                            <el-table
                                :data="tableData1"
                                border
                                style="width: 100%">
                                <el-table-column prop="tdzsc" label=""></el-table-column>
                                <el-table-column prop="tdpjjgsc" label=""></el-table-column>
                                <el-table-column prop="sffstdld" label=""></el-table-column>
                                <el-table-column prop="wltdmgd" label="15"></el-table-column>
                            </el-table>
                            <el-divider></el-divider>
                            <div class="td_title"></div>
                            <el-table
                                :data="tableData2"
                                border
                                style="width: 100%">
                                <el-table-column prop="tdlxgdx" label=""></el-table-column>
                                <el-table-column prop="tdcs" label=""></el-table-column>
                                <el-table-column prop="tdsjldxdb" label=""></el-table-column>
                            </el-table>
                            <el-divider></el-divider>
                            <div class="td_title"></div>
                            <el-table
                                :data="tableData3"
                                border
                                style="width: 100%">
                                <el-table-column prop="fscftzby" label=""></el-table-column>
                                <el-table-column prop="fswtzby" label=""></el-table-column>
                                <el-table-column prop="jhtdqstldzb" label="3"></el-table-column>
                            </el-table>
                            <el-table
                                :data="tableData3"
                                border
                                style="width: 100%">
                                <el-table-column prop="dqtddycdxsb" label=""></el-table-column>
                                <el-table-column prop="lxfx" label=""></el-table-column>
                            </el-table>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>

<!--        //-->
        <el-dialog
            class="successList-dialog"
            :visible.sync="successListVisible"
            width="90%"
            top="5vh"
            :before-close="successListHandleClose"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
        >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '' }}</span>
      </span>
            <div class="dialog-content">
                <div class="dialog-content-title" style="margin-bottom: 10px">
                    <el-select
                        size="small"
                        class="input_td"
                        v-model="selectParams.dsjVal"
                        clearable
                        placeholder=""
                        @change="dsjChange"
                    >
                        <el-option
                            v-for="item in selectParams.dsjOptions "
                            :key="item.ORG_CODE"
                            :label="item.ORG_NAME"
                            :value="item.ORG_CODE"
                        ></el-option>
                    </el-select>
                    <el-select
                        v-model="selectParams.xqjVal"
                        clearable
                        placeholder=""
                        class="input_td"
                        size="small"
                        @change="qxjChange"
                    >
                        <el-option
                            v-for="item in selectParams.xqjOptions"
                            :key="item.ORG_CODE"
                            :label="item.ORG_NAME"
                            :value="item.ORG_CODE"
                        ></el-option>
                    </el-select>
                    <el-select
                        size="small"
                        class="input_td"
                        v-model="selectParams.gdsVal"
                        clearable
                        placeholder=""
                    >
                        <el-option
                            v-for="item in selectParams.gdsOptions"
                            :key="item.ORG_CODE"
                            :label="item.ORG_NAME"
                            :value="item.ORG_CODE"
                        ></el-option>
                    </el-select>
                    <el-date-picker
                        size="small"
                        class="input_td"
                        v-model="successListQueryForm.time"
                        type="date"
                        format="yyyy  MM  dd "
                        value-format="yyyy-MM-dd"
                        placeholder=""
                    ></el-date-picker>
                    <el-input
                        class="input_td"
                        placeholder=""
                        v-model="selectValue"
                        size="small"
                        clearable
                    ></el-input>
                    <el-button size="small" type="primary" @click="getSuccessListData"></el-button>
                </div>
                <div class="dialog-content-main" style="height: 600px">
                    <bdaTable
                        :tableData="successListData"
                        :isPage="true"
                        :height="600"
                        :pageConfig="successListPageConfig"
                        @changePage="changePage"
                    ></bdaTable>
