<script>
  import Interface1 from '@/base/mixin/power-sensitive-users';
  import Interface2 from '@/base/mixin/common-api';
  import Interface3 from '@/base/mixin/electricity-behavior';
  import wordCloud from "../../electricityBehavior/components/wordCloud";
  import bdaTable from '@/components/bda-table/bda-table'
  export default {
    name: "differenceServiceSupervision",
    mixins: [Interface1,Interface2,Interface3],
    components:{
      wordCloud,
      bdaTable
    },
    props:{

    },
    data(){
      return {
        //
        selectParams:{
          dsjOptions:[],
          dsjVal:'',
          xqjOptions:[],
          xqjVal:'',
          gdsOptions: [],
          gdsVal:''
        },
        yhCodeInput:'',
        tdgdhInput:'',
        //
        pages:{
          pageNo:1,
          pageSize:10,
          total:11,
        },
        tableData:[],
        loading:false,
        //
        editTsTimeShow:false,
        tsTime:'',
        code:'',
        // 
        portraitInfoVisible: false,
        portraitInfoQueryForm: {
          yhbh: "",
          dxtznr: '',
          dxfssj: "",
          lxfs: ""
        },
        tableData1:[],//
        tableData2:[],//
        tableData3:[],//

        //
        wordCloudData: [],
        wordCloudDataVisible: false,

        //
        successListVisible:false,
        successListQueryForm: {
          time: "",
          yhbh: "",
          current: 1,
          size: 10,
        },
        gddwOptions: [],
        gdsOptions: [],
        xqjOptions: [],
        selectValue: '',
        successListData: {},
        successListPageConfig: {
          name: "successList",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
      }
    },
    mounted() {
      this.GetPowerAreaCode()//
      this.GetPlanPowerEventChecklist()
    },
    methods:{
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.GetPlanPowerEventChecklist()
        window.console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.GetPlanPowerEventChecklist()
        window.console.log(`: ${val}`);
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex%2===0) {
          return 'success-row';
        }
        return '';
      },
      //--
      GetPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "2",//2====3==4===
          // "orgCode":"0306" //
        }).then(res => {
          this.selectParams.dsjOptions = res
        })
      },
      //--
      GetQxjPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "3",//2====3==4===
          "orgCode":this.selectParams.dsjVal //
        }).then(res => {
          this.selectParams.xqjOptions = res
        })
      },
      //
      GetGdsPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "4",//2====3==4===
          "orgCode":this.selectParams.xqjVal //
        }).then(res => {
          this.selectParams.gdsOptions = res
        })
      },
      //
      dsjChange(){
        this.selectParams.gdsVal = ''
        this.selectParams.gdsOptions =[]
        this.selectParams.xqjVal=''
        this.selectParams.xqjOptions =[]
        this.GetQxjPowerAreaCode()
      },
      //
      qxjChange(){
        this.selectParams.gdjVal = ''
        this.selectParams.gdsOptions =[]
        this.GetGdsPowerAreaCode()
      },

      //
      GetPlanPowerEventChecklist(){
        this.getPlanPowerEventChecklist({
          "dsj":this.selectParams.dsjVal,
          "xqj":this.selectParams.xqjVal,
          "gds":this.selectParams.gdsVal,
          "yhbh":this.yhCodeInput,
          "tdgdh":this.tdgdhInput,
          "size":this.pages.pageSize,
          "current":this.pages.pageNo
        }).then(res=>{
          this.pages.total = res.total
          this.tableData = res.list
        })
      },
      //
      DeleteMessageSendTask(row){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteMessageSendTask({
            code:row.code,
            yhbh:row.yhbh,
            tdgdh:row.orderCode,
          }).then(()=>{
            this.GetPlanPowerEventChecklist()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      openEditSendTime(row){
        this.code = row.code
        this.tsTime = row.planPushSysDate||''
        this.editTsTimeShow = true;
      },
      //
      setTsTime(){
        this.updateMessagePushDate({
          code:this.code,
          planPushDate:this.tsTime,
        }).then(()=>{
          this.editTsTimeShow = false;
          this.GetPlanPowerEventChecklist()
        })
      },
      //
      // 
      portraitInfoHandleClose() {
        this.portraitInfoVisible = false
      },
      //
      GetTdmgkhhx(row=null){
        if (row.yhbh){
          this.portraitInfoQueryForm.yhbh  = row.yhbh
        }
        this.portraitInfoVisible=true
        this.getWordCloudData()
        this.GetRecommendations()
        this.getTdmgkhhx({
          yhbh:this.portraitInfoQueryForm.yhbh,
        }).then((res)=>{
          this.tableData1 = [res.tmNpmisTdmgjsjgkhxxT]
          this.tableData2 = [res.tmNpmisTdmgjhgjkhxxT]
          this.tableData3 = [res.tmNpmisTdmgdrcdxtzsfjsT]
          window.console.log(res)
        })
      },
      //
      getWordCloudData() {
        this.wordCloudDataVisible = false
        this.getUserPortraitInfo({
          yhbh: this.portraitInfoQueryForm.yhbh
          // yhbh: '32123'
        }).then(res => {
          let wordCloudData = []
          if (res && res.length !== 0) {
            res.forEach(e => {
              wordCloudData.push({
                name: e,
                value: parseInt(Math.random() * 100)
              })
            })
          }
          this.wordCloudData = wordCloudData
          this.wordCloudDataVisible = true
        })
      },
      //
      GetRecommendations() {
        this.getRecommendations2({
          "yhbh": this.portraitInfoQueryForm.yhbh
        }).then(res => {
          this.portraitInfoQueryForm.dxtznr = res.MESSAGE_SEND_CONTENT
          this.portraitInfoQueryForm.lxfs = res.LXDH
          this.portraitInfoQueryForm.dxfssj = res.SMS_SEND_TIME
        })
      },

      //
      // 
      successListHandleClose() {
        this.successListVisible = false
      },
      getSuccessListData() {
        let columnNameData = [
          { prop: 'id', label: '' },
          { prop: 'sendToYxTimePlan', label: '' },
          { prop: 'sendToYxTime', label: '' },
          { prop: 'tdgdh', label: '' },
          { prop: 'yhbh', label: '' ,},
          { prop: 'yhmc', label: '' },
          { prop: 'yddz', label: '',width: 200 },
          { prop: 'dsj', label: '' },
          { prop: 'xqj', label: '' },
          { prop: 'gds', label: '' },
          { prop: 'lxdh', label: '' },
          { prop: 'content', label: '', width: 300 },
          { prop: 'smsSendTime', label: '' }
        ]
        this.getPlanPowerEventChecklist({
          "dsj": this.selectParams.dsjVal,
          "xqj": this.selectParams.xqjVal,
          "gds": this.selectParams.gdsVal,
          "page": this.successListQueryForm.current,
          "size": this.successListQueryForm.size,
          "yhbh": this.selectValue,
          "tsrq": this.successListQueryForm.time,
        }).then(res => {
          this.loading = false
          this.successListPageConfig.total = res.total
          this.successListData = {
            tableData: [
              ...res.list
            ],
            headerColor: '#1899fa',
            title: "",
            name: 'successList',
            size: this.successListQueryForm.size,
            current: this.successListQueryForm.current,
            total: this.successListPageConfig.total,
            columnNameData
          }
        })

      },
      //
      changePage(data) {
        if (data.name === 'successList') {
          this.successListQueryForm.size = data.size
          this.successListQueryForm.current = data.current
          this.getSuccessListData()
        }
      },
      // 
      clickSuccessList() {
        this.successListVisible = true
        this.getSuccessListData()
      },
    }
  }
</script>

<style scoped lang="scss">
    .differenceServiceSupervision{
        .input_td{
            width: 12%;
            margin-right: 20px;
        }
        .input_300{
            width: 12%;
            margin-right: 20px;
        }
        .header{
            line-height: 30px;
            .selectNav{
                height: 30px;
                margin-bottom: 16px;
            }

        }
        .content{
            .title{
                padding-left: 18px;
                border-left: 6px solid #1A84F4;
                font-weight: bold;
                margin-bottom: 10px;
            }
            .tableTd{
                .block{text-align: center;margin-top: 10px}

            }
        }
        .portraitInfo-dialog {
            ::v-deep .el-table .warning-row {
                background: #e9f3fe;
            }
            ::v-deep .el-dialog__header {
                padding: 10px 0 10px;
                border-bottom: 1px solid #e6e6e6;
            }
            ::v-deep .el-dialog__body {
                padding: 10px;
            }
            ::v-deep .el-dialog__footer {
                padding: 10px 10px 10px 0;
                border-top: 1px solid #e6e6e6;
                text-align: center;
            }
            .dialog-header {
                .header-text {
                    margin-left: 20px;
                    color: #808080;
                    font-size: 14px;
                }
            }
            .dialog-chart {
                height: 400px;
            }
        }
        ::-webkit-scrollbar{
            height: 10px;
        }
        .portraitInfo-dialog {
            .dialog-content-title {
                height: 42px;
                display: flex;
                align-items: center;
            }
            .dialog-content-main {
                height: 760px;
                display: flex;
                padding: 0 20px;
                .main-title {
                    height: 32px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: #0056d7;
                    font-weight: bold;
                }
                .recommend {
                    width: 20%;
                    border: 1px solid #72ccff;
                    margin-right: 20px;
                    padding: 0 10px 10px;
                    .text-item {
                        display: block;
                        margin-bottom: 20px;
                        font-style: 16px;
                        .text {
                            display: block;
                            margin-bottom: 10px;
                            font-weight: bold;
                            font-size: 15px;
                        }
                    }
                }
                .portrait-label {
                    width: 40%;
                    margin-right: 20px;
                    .main-content {
                        height: calc(100%);
                        border: 1px solid #72ccff;
                    }
                }
                .calendar {
                    width: 50%;
                    .main-content {
                        height: calc(100% - 20px - 20px);
                        margin-bottom: 20px;
                        border: 1px solid #72ccff;
                        padding:10px 6px;
                        .td_title{
                            border-left: 6px solid #1A84F4;
                            height: 20px;
                            line-height: 20px;
                            padding-left: 10px;
                            margin: 10px 0 10px 0;
                            color: #1A84F4;
                        }
                    }
                }
            }
        }
    }
</style>